import { PsButtonApproveComponent } from './../../../commonSRC/psComponents/ps-base/ps-base-action/ps-action-button/ps-button-approve/ps-button-approve.component';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IMapKeyValue, IOptionsPsButtonApprove, IOptionsPsContainerLookupOptionComponentExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { CommonBussinessConstant } from '../../psServices/models/ps-common-bussiness-constant';
import { IListContainerLookupOption } from '../../psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from '../../psServices/models/ps-commonBusiness.settings';
import { IOmniMandatesApproval, IOptionsPsMandatesListExposed } from './ps-mandates-list.component.interface';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsComplexAlertController } from 'src/app/commonSRC/psTemplates/ps-template-alert-controller/ps-template-alert-controller.template.interfaces';
import { OmniPullService } from '../../psServices/omni-common/omni-pull.service';

@Component({
  selector: 'ps-mandates-list',
  templateUrl: './ps-mandates-list.component.html',
  styleUrls: ['./ps-mandates-list.component.scss'],
})
export class PsMandatesListComponent extends PsTemplateListPage implements OnInit {

  @Input() options: IOptionsPsMandatesListExposed;
  @Output() public onClickCard = new EventEmitter<any>();
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  constructor(private omniPull?: OmniPullService) {
    super();
  }

  ngOnInit() {
    super.init();
    if (this.options && this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }
    this.populateMandatesListMap();
  }

  public updateCard(mandates: any) { }


  prepareLookUp(card: IOmniMandatesApproval): any {
    let lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.itemList = this.options.itemList;
    lookupDetails.showInitialCardValues = 6;
    lookupDetails.currencyFlag = 'currencyBriefNameEnglish';


    lookupDetails.actionDetailsOptions = [
      {
        id: 'approve',
        labelOptions: {
          labelKey: 'approve_key'
        },
        buttonIcon: 'approve',
        actionUrl: PsCommonBusinessSettings.serviceUrl.approveMandates,
        cssClass: 'fab-edit-button',
        label: 'approve_key',
        iconName: 'paper-plane',
        actionType: 'approve',
        alertType: 'prompt',
        fcName: 'approveMandates',
      },
      {
        id: 'reject',
        labelOptions: {
          labelKey: 'reject_key'
        },
        buttonIcon: 'reject',
        actionUrl: PsCommonBusinessSettings.serviceUrl.rejectMandates,
        cssClass: 'fab-edit-button',
        label: 'reject_key',
        iconName: 'trash',
        actionType: 'reject',
        alertType: 'prompt',
        fcName: 'rejectMandates',
      },
    ];
    return lookupDetails;
  }

  private populateMandatesListMap() {
    let dateMap: IMapKeyValue = {};
    let beneficiaryNameMap: IMapKeyValue = {};
    let payIdTypeMap: IMapKeyValue = {};
    let amountMap: IMapKeyValue = {};
    let frequencyMap: IMapKeyValue = {};
    let dateRangeMap: IMapKeyValue = {};
    let mobileNumberMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};

    dateMap = {
      key: 'date',
      value: '--- --- ---',
      isEdit: false
    };

    beneficiaryNameMap = {
      key: 'benefName',
      value: '------',
      isEdit: false
    };

    payIdTypeMap = {
      key: 'payIdType',
      value: '------',
      isEdit: false
    };

    amountMap = {
      key: 'mandatesAmount',
      value: '------',
      isEdit: false
    };
    frequencyMap = {
      key: 'frequency',
      value: '------',
      isEdit: false
    };
    dateRangeMap = {
      key: 'dateRange',
      value: '------',
      isEdit: false
    };
    mobileNumberMap = {
      key: 'benefPhone',
      value: '------',
      isEdit: false
    };

    headerSubtitleMap = {
      key: 'title',
      value: 'date',
      isEdit: false
    };

    headerTitleMap = {
      key: 'subTitle',
      value: 'benefName',
      isEdit: true
    };

    // key here will be used as label in look up details component
    this.headerMap.set('benefName', headerSubtitleMap);
    this.headerMap.set('date', headerTitleMap);

    this.labelValuesMap.set('amount_key', amountMap);
    this.labelValuesMap.set('mobile_number_key', mobileNumberMap);
    this.labelValuesMap.set('pay_id_type_key', payIdTypeMap);
    this.labelValuesMap.set('frequency_key', frequencyMap);
    this.labelValuesMap.set('date_range_key', dateRangeMap);



  }
}
